import Vue from 'vue'
import Router from 'vue-router'
import Resume from '@/components/page/Resume'
import javaScriptHome from '@/components/page/javascript/javaScriptHome'
import VueHome from '@/components/page/vue/VueHome'
import WebPackHome from '@/components/page/webpack/WebpackHome'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Resume',
      component: Resume
    },
    {
      path: '/javascript',
      name: 'javaScriptHome',
      component: javaScriptHome
   }
   ,
    {
      path: '/vuehome',
      name: 'VueHome',
      component: VueHome
   }
   ,
    {
      path: '/webpackhome',
      name: 'WebPackHome',
      component: WebPackHome
   }   
  ]
})



