<template>
  <div class="resume">
      <div> 
        <div class="weui-cell my_titel" aria-controls="">
            <div class="weui-cell__hd"><img class="my_pic" src="../../assets/img/my.jpg"/></div>
            <div class="weui-cell__bd">
                <p style="color:#999">李棠寅</p>
            </div>
            <div class="weui-cell__ft" style="margin-right:33px;">分数{{this.allNmuber}}</div>
        </div>
      </div>
    <div v-if="test" style="padding-top:55px;padding-bottom:40px;">
      <div class="weui-cell weui-cell_access" v-for="item in items">
          <div class="weui-cell__bd" style="position: relative;">
              <span class="my_test" style="vertical-align: middle">{{item.test}}</span>
              <span class="weui-badge" style="margin-left: 5px;position: absolute;right: 10px;top:6px;">{{item.num}}</span>
          </div>
      </div>
    </div>  
    <div v-if="!test" style="background:#000;color:#fff;height:1000px;width:100%;padding:75px 35px 0 15px;">
      <div>
      <h4>1你能觉得你想要的幸福是什么？</h4>
      <p>回答不了</p>
      <h4>2你觉得什么类型的女士跟你组建一个家庭你会幸福？</h4>
      <p style="margin-bottom:15px;padding-right:20px">这个也回答不了，不过我脾气好。只要是个正常人都可以过。</p>
      <h4>3你觉得自己在过去的情感经历中你到不足点是什么？改变计划是什么？需要红娘如何协</h4>
      <p style="margin-bottom:15px">搞不定丈母娘，这个算吗？要不早结婚了</p>
      <h4>4今后你对于事业的规划是什么？</h4>
      <p style="margin-bottom:15px;padding-right:20px">先走技术比较技术能给我丰厚的回报，会持续一段时间看技术，技术更新迭代。等稳定了才会技术转型实体。</p>
      <div class="weui-footer">
        <p class="weui-footer__links">
            <a href="javascript:void(0);" class="weui-footer__link">本站由屌丝李棠寅提供，翻版不追究</a>
        </p>
        <p class="weui-footer__text">Copyright © 2008-2016 weui.io</p>
      </div>
      </div>
    </div>
      <div class="footer" style="border-top:1px solid #ccc">
        <div class="weui-flex">
            <div class="weui-flex__item"><div class="placeholder" @click="showtest">人格测试</div></div>
            <div class="weui-flex__item"><div class="placeholder" @click="showogher">其他</div></div>
        </div>      
      </div>       
  </div>
</template>
<script>
export default {
  name: "resume",
  data() {
    return {
      isActive: "first",
      allNmuber: 0,
      test: true,
      items: [
        { test: "1:我总是注意到别人的错误，并向他直接指出来", num: 1 },
        { test: "2:大家觉得我总是一脸严肃，表情生硬。", num: 1 },
        { test: "3:我力求尽善尽美，常常反思不足、自我批评、努力改进", num: 5 },
        {
          test: "4：我期望一切井然有序，无法对明知的错误或混乱视而不见",
          num: 1
        },
        { test: "5:大家觉得我应该放松点，多一点人情味", num: 1 },
        { test: "6:只有每个步骤及细节都做对了，结果才会正确", num: 5 },
        { test: "7:帮不到别人的时候我感到特别难受", num: 5 },
        { test: "8:我高兴帮助别人，却不习惯需要别人的帮助", num: 5 },
        { test: "9:我时常为别人的事四处跑，忙得不亦乐乎", num: 1 },
        { test: "10:当没有为别人的事而忙碌时，我会感到寂寞", num: 1 },
        { test: "11:大家很乐意向我诉说他们的困难", num: 5 },
        { test: "12:我会基于同情心站在朋友一边，即使他们犯错了", num: 5 },
        { test: "13:我不觉得向别人展示自己的成功有什么难为情的", num: 5 },
        { test: "14:我喜欢当主角，受到众人的关注", num: 1 },
        { test: "15:我能轻易说服别人", num: 1 },
        { test: "16:我办事效率高，能迅速模仿别人快捷的办法", num: 5 },
        { test: "17:我精力充沛，喜欢不断追求更多的成功", num: 1 },
        { test: "18:不管什么场合，人们都能看到我最棒的一面", num: 5 },
        { test: "19:我特别能感受悲伤，有时甚至享受这种感觉", num: 1 },
        { test: "20:我缺少很多别人拥有的东西", num: 1 },
        { test: "21:我常常有被遗弃的感觉", num: 5 },
        { test: "22:初见我的人觉得我表现冷漠而高傲", num: 5 },
        { test: "23:我常常不知道自己下一刻想要什么。", num: 1 },
        { test: "24:我常在同一天内感受到喜怒哀乐的变化", num: 1 },
        { test: "25:探索自然的奥秘，我感到其乐无穷", num: 5 },
        { test: "26:我很少主动去接近别人", num: 5 },
        { test: "27:我很少快速做出判断，而需要周密地考虑。", num: 5 },
        { test: "28:要在众人面前表现自己会使我不自在甚至有点怯场", num: 5 },
        { test: "29:独自在宽敞的资料室收集各种信息，是一种享受", num: 5 },
        { test: "30:我特别需要独处的时间", num: 5 },
        { test: "31:我总是设想最坏的结果，然后陷入苦恼之中", num: 1 },
        { test: "32:我常常试探或考验朋友的忠诚", num: 1 },
        { test: "32:所有罪名中，让我最厌恶的是欺骗", num: 1 },
        { test: "33:我有时自信自己掌握了权威，有时却想依赖别人的决定", num: 5 },
        { test: "34:上司最看重我尽忠职守，朋友也觉得我忠诚可靠", num: 1 },
        { test: "35:面对威胁，我一时焦急忧虑，一时激烈反抗", num: 5 },
        { test: "36:我很在意自己是否依然年轻有活力，常担心自由被剥夺", num: 5 },
        {
          test:
            "37:我时常发现新开张的美食店，最新的服装款式，或新兴的游乐节目。",
          num: 1
        },
        { test: "38:我觉得有很多新鲜有趣的事情，人生真是好玩又快乐", num: 5 },
        { test: "39:我总是有许多新鲜有趣的计划，时间怎么也不够用", num: 5 },
        {
          test:
            "40:我只喜欢与有趣的人为友，对一些闷蛋却懒得交往，即使他们看来很有深度",
          num: 5
        },
        { test: "41:我很少用心去听别人的心情，只喜欢说说俏皮话和笑话", num: 1 },
        { test: "42:我看不起那些软弱的人", num: 1 },
        { test: "43:我享受掌握权力的感觉，喜欢迎接挑战", num: 1 },
        { test: "44:对那些行为过分的人，我一定好好教训他们", num: 5 }
      ]
    };
  },
  methods: {
    showtest: function() {
      this.test = true;
    },
    showogher: function() {
      document.body.scrollTop = document.documentElement.scrollTop = 0;
      this.test = false;
    }
  },
  mounted: function() {
    var localNnumber = 0;
    for (var i = 0; i < this.items.length; i++) {
      localNnumber += this.items[i].num;
      console.log(this.items[i].num);
    }
    this.allNmuber = localNnumber;
  }
};
</script>
<style>
.my_test {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: inline-block;
  width: 300px;
}
.my_pic {
  height: 35px;
  width: 35px;
  border-radius: 50%;
  margin-right: 15px;
}
.my_titel {
  position: fixed;
  top: 0px;
  background: #ffffff;
  width: 100%;
  z-index: 9999;
}
.footer {
  position: fixed;
  bottom: 0px;
  width: 100%;
  background: #fff;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: #999;
  z-index: 9999;
}
</style>



